  transition: color .3s;
  position: relative;
  overflow: hidden;
}

.call-action button:before, .call-action button:after {
  content: "";
  z-index: -1;
  transform-origin: 0 0;
  background-color: #020203;
  width: 0%;
  height: 100%;
  transition: all .6s;
  position: absolute;
  top: 0;
  left: -70%;
